body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
  max-width: 375px;
  margin: 0 auto;
  padding: 30px;
  cursor: pointer;
}

.selectFile input {
  background: #C94D46;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
}

.slider input {
    width: 80%;
/*  width: 60%; */
  margin: auto;
  cursor: pointer;
}
.shortSlider input {
  width: 60%;
  margin: auto;
  cursor: pointer;
}

.addClicks button {
  background: #C94D46;
  padding: 8px;
  border: 1px solid #832420;
  border-radius: 2px;
  width: 80px;
/*  width: 60px; */
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.tinyButton button {
  cursor: pointer;
  height: 25px;
  width: 25px;
  margin: 0 auto;
  text-align: center;
  line-height: 10px;
  border-radius: 5px;
  font-size: 20px;
  background: #00ffff;
  padding: 5px 5px;
  color: #000000;
}

.small-button button {
  background: #0000ff;
  padding: 8px;
  border: 1px solid #832420;
  border-radius: 2px;
  width: 50px;
  color: #fff;
  font-size: 15px;
  cursor: pointer;
}

